body {
  transition: .5s all ease;
}

button:focus,
textarea:focus,
select:focus {
  outline: none !important;
  -webkit-appearance: none;
  box-shadow: none;
}

input:focus {
  outline: none !important;
}

a:active,
a:focus {
  outline: 0;
  outline: none !important;
  border: none;
  box-shadow: none;
}

a,
img,
input,
button,
span {
  transition: .3s all ease;
}

.btn-main:hover {
  color: #fff;
}

a:hover {
  color: inherit
}

.wrapper {
  overflow: hidden;
}


.reviews-i .rev-icon {
  position: absolute;
  transform: translate(-50%, -50%);
  transition: .3s;
  left: 50%;
  top: 50%;

  @include mobile {
    width: 30px;
    height: 30px;
    @include flex-all-center;



    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
}

.reviews-i .rev-icon:hover img {
  transform: scale(1.1);
}

.reviews-i .rev-icon img {
  width: 70px;
  height: 70px;
}

.reviews-i::before {
  content: none !important;
}

.brends {
  position: relative;
}

.brends .brends-bg {
  position: absolute;
