}

footer .contacts-i {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}

#scroll-top {
  position: fixed;
  right: 10px;
  bottom: 70px;
  cursor: pointer;
  width: 50px;
  height: 50px;
  background-color: #39BCFF;
  text-indent: -9999px;
  border-radius: 4px;
  z-index: 11;
  opacity: 0;
  visibility: hidden;
  transition: opacity 1s ease-in-out, visibility 1s ease-in-out;

  &.show {
    opacity: 1;
    visibility: visible;
  }
}

#scroll-top img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.extra-btn {
  margin-top: 30px;
}

.brends-list__item:hover img,
.advantages-list__item:hover img,
.hero-list li:hover img {
  transform: scale(1);
}

.hero-list li:hover::before,
.advantages-list__item:hover::before {
  background: linear-gradient(-52deg, #bca59f 0, #fff 31.36%, #fff 64.49%, #bca59f 100%);
}

.about-list__item:hover,
.advantages-list__item:hover {
  box-shadow: none;
}

