.product-slide .swiper {
  width: 100%;
  height: 300px;
  margin-left: auto;
  margin-right: auto;

  .swiper-slide {
    display: flex;
  }
}

.swiper-slide {
  background-size: cover;
  background-position: center;
}

.product-slide .mySwiper2 {
  height: 80%;
  width: 100%;
}

.product-slide .mySwiper {
  height: 20%;
  box-sizing: border-box;
  padding: 10px 0;
}

.product-slide .mySwiper .video {
  position: relative;
  transition: .2s;
}

.product-slide .mySwiper .video .play {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: .2s;
}

.product-slide .mySwiper .video .play img {
  width: 30px;
  height: 30px;
  transition: .2s;
}

.product-slide .mySwiper .video:hover .play img {
  width: 35px;
  height: 35px;
}

.product-slide .mySwiper .swiper-slide {
  width: 25%;
  height: 100%;
  cursor: pointer;
  opacity: 0.8;
}

.product-slide .mySwiper .swiper-slide-thumb-active {
  opacity: 1;
}

.product-slide .swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
}

.product-slide .mySwiper .swiper-slide img {
  height: 70px;
  border-radius: 4px;
}

.swiper-button-next:after,
.swiper-button-prev:after {
  content: "";
}

