/* ====================== custom select ========== */
.select-hidden {
  display: none;
  visibility: hidden;
  padding-right: 10px;
}

.select {
  cursor: pointer;
  display: inline-block;
  position: relative;
  font-size: 14px;
  line-height: 130%;
  color: #414042;
  width: 100%;
  height: 50px;
}

.select-styled {
  display: flex;
  align-items: center;
  height: 50px;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 2px 10px;
  transition: all 0.2s ease-in;
  font-size: 14px;
  line-height: 130%;
  color: #414042;
  background: #FFFFFF;
  border-radius: 4px;
  overflow: hidden;
}

.select-styled:after {
  content: "";
  background-image: url('../images/icons/select-arrow.svg');
  background-repeat: no-repeat;
  background-size: auto;
  width: 12px;
  height: 7px;
  transition: .3s all ease;
  position: absolute;
  top: 22px;
  right: 10px;
}

.select-styled.active::after {
  transform: rotate(180deg);
}

.select-options {
  display: none;
  position: absolute;
  top: 102%;
  right: 0;
  left: 0;
  z-index: 999;
  margin: 0;
  padding: 0;
  list-style: none;
  border-radius: 4px;
}

.select-options li {
  margin: 0;
  padding: 2px 10px;
  text-indent: 15px;
  transition: all 0.15s ease-in;
  font-size: 14px;
  line-height: 130%;
  color: #414042;
  background: #fff;
  height: 50px;
  display: flex;
  align-items: center;
  border-top: 1px solid #7b7b7b;
}

.select-options li:first-child {
  border-top: none;
}

.select-options li[rel="hide"] {
  display: none;
}

