  content: none;
}

/* custom checkbox */
.control {
  display: block;
  position: relative;
  cursor: pointer;
  padding-left: 30px;
}

.control input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.control__indicator {
  position: absolute;
  top: 2px;
  left: 0;
  height: 19px;
  width: 19px;
  background: transparent;
  border: 1px solid #fff;
}

.control input:disabled~.control__indicator {
  background: #e6e6e6;
  opacity: 0.6;
  pointer-events: none;
}

.control__indicator:after {
  content: '';
  position: absolute;
  display: none;
}

.control input:checked~.control__indicator:after {
  display: block;
}

.control--checkbox .control__indicator:after {
  left: 4px;
  top: 4px;
  width: 9px;
  height: 9px;
  background: #fff;
}

.control--checkbox input:disabled~.control__indicator:after {
  border-color: #7b7b7b;
}

