.product-slide .swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.video-full .play img {
  width: 60px !important;
  height: 60px !important;
  transition: .2s;
}



.russian-map {

  .rus-map img,
  .rus-map svg {
    width: 100%
  }
}

.rus-map svg g,
.rus-map svg path,
.rus-map svg rect {
  cursor: pointer;
  transition: transform .25s ease-in-out, fill .25s ease-in-out;
  transform-origin: 50% 70%
}

.rus-map svg g:hover rect {
  fill: #53c5e2
}

@media (max-width: 767px) {
  .rus-map {
    overflow: auto
  }

  .rus-map svg {
    width: 1300px !important
  }
}

.instruction-tab-wrapper {
  display: none;
  animation: fade .25s ease-in-out;

  &.active {
    display: block;
  }
}

.swiper.slide-images {
  width: 100%;
}